/* primary */
@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  src:
    url('./roboto-v29-latin_cyrillic-regular.woff2') format('woff2'),
    url('./roboto-v29-latin_cyrillic-regular.woff') format('woff');
}

/* primaryItalic */
@font-face {
  font-family: Roboto;
  font-style: italic;
  font-weight: 300;
  src:
    url('./roboto-v29-latin_cyrillic-300italic.woff2') format('woff2'),
    url('./roboto-v29-latin_cyrillic-300italic.woff') format('woff');
}

/* primaryBold */
@font-face {
  font-family: Roboto;
  font-style: normal;
  font-weight: 700;
  src:
    url('./roboto-v29-latin_cyrillic-700.woff2') format('woff2'),
    url('./roboto-v29-latin_cyrillic-700.woff') format('woff');
}

/* secondary */
@font-face {
  font-family: 'Roboto Slab';
  font-style: normal;
  font-weight: 400;
  src:
    url('./roboto-slab-v16-latin_cyrillic-regular.woff2') format('woff2'),
    url('./roboto-slab-v16-latin_cyrillic-regular.woff') format('woff');
}

/* brand */
@font-face {
  font-family: 'Roboto Condensed';
  font-style: normal;
  font-weight: 300;
  src:
    url('./roboto-condensed-v19-latin-300.woff2') format('woff2'),
    url('./roboto-condensed-v19-latin-300.woff') format('woff');
}
